import React, {Component} from 'react';
import '../Assets/css/global.css'
import '../Assets/css/cloud.css'
import Set1 from '../Assets/images/cloud/set.jpg'
import Set2 from '../Assets/images/cloud/set2.jpg'
class Cloud extends Component {
    render(){
        return(
            <div className='cloud__wrapper'>
                <p className='title'>Professional security data <span className='title__sp--blue'>stored in cloud.</span></p>
                <div className='cloud__set--wrapper'>
                    <div className='cloud__set--line'></div>
                    <div className='cloud__image--wrapper'>
                        <div className='cloud__image'>
                            <img src={Set1} className='cloud__set--image' alt='security camera cloud set' />
                            <p>All in one professional security package</p>
                        </div>
                        <div className='cloud__image'>                        
                            <img src={Set2} className='cloud__set--image' alt='security camera cloud set' />
                            <p>All in on expandable scurity package</p>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Cloud;