import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'
import './App.css';
import Nav from './Components/Nav';
import Home from './Components/Home'
import Header from './Components/Header';
import Explore from './Components/Explore'; 
import Footer from './Components/Footer'; 
import AllProducts from './Components/SubComponents/AllProducts'
import Reseller from './Components/SubComponents/Reseller'
import AllNews from './Components/SubComponents/AllNews'


class App extends Component {


  render() {
    return (
      <Router>
      <div className="App">
        <Nav />
        <Header />
        <Route exact path="(/home|/security|/)" component={Home} />
        <Route path='/products' component={AllProducts} />
        <Route path='/explore' component={Explore} />
        <Route path='/resellers' component={Reseller} />
        <Route path='/news' component={AllNews} />
        <Route path='/eshop' render={()=> <p>shop page</p>} />
        <Route path='/support' render={()=> <p>support page</p>} />
        <Footer />
        
      </div>
  </Router>
    );
  }
}

export default App;
