import React, { Component } from 'react';
import '../Assets/css/product_info.css'

class ProductInfo extends Component {
  constructor(props){
    super(props)
    this.state = {
      data: this.props.data
    }
   
  }

  componentDidMount(){
    if(this.props.data !== undefined){
      this.setState({data:this.props.data})
    }

  }

  render() {
    const {data} = this.state 

    return (
      <div className='product__info--wrapper'>
        {
           data !== null ?
          
               <div>
                  <div className='product__description'>
                    <p className='product__description--text'>
                      {
                        data.description.length !== 0 ? data.description : <span style={{textAlign:'center'}}>No description for this product</span>
                      }
                    </p>
                  </div>
                  <table>
                    <tbody>
                    
                    <tr>
                      <th>Conectivity</th>
                      <td>
                        {data.conect}
                      </td>
                    </tr>
                    
                    <tr>
                      <th>Video Resolution</th>
                      <td>
                        {data.video_resolution}p
                      </td>
                    </tr>
                    
                    <tr>
                      <th>Frames</th>
                      <td>
                        {data.frames}
                      </td>
                    </tr>
                    <tr>
                      <th>Notification</th>
                      <td>
                        {
                          data.notification.length !== 0 ?
                          data.notification.map((noti,index)=> {
                            return(
                              <span key={index}>
                                {noti}{index + 1 === data.notification.length  ? '' : ', '} 
                              </span>
                            )
                          } ) :
                          'no notification'
                        }
                      </td>
                    </tr>

                    <tr>
                      <th>Smart Home</th>
                      <td>
                      {
                          data.smart_home.length !== 0 ?
                          data.smart_home.map((sm,index)=> {
                            return(
                              <span key={index}>
                                {sm}{index + 1 === data.smart_home.length  ? '' : ', '} 
                              </span>
                            )
                          } ) :
                          'no smart home'
                        }
                      </td>
                    </tr>

                    <tr>
                      <th>Storage</th>
                      <td>
                      {
                          data.storage.length !== 0 ?
                          data.storage.map((s,index)=> {
                            return(
                              <span key={index}>
                                {s}{index + 1 === data.storage.length  ? '' : ', '} 
                              </span>
                            )
                          } ) :
                          'no storage'
                        }
                      </td>
                    </tr>

                    <tr>
                      <th>Alarm</th>
                      <td>
                        {
                          data.alarm === 'yes' ? 'Yes' : 'No'
                        }
                      </td>
                    </tr>

                    <tr>
                      <th>Two Way Audio</th>
                      <td>
                        
                        {
                          data.two_way_audio === 'yes' ? 'Yes' : 'No'
                        }
                      </td>
                    </tr>

                    
                    </tbody>
                  </table>
            </div>
            :
            "loading"
        }
      </div>
    )
  }
}

export default ProductInfo;
