import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom'
import '../Assets/css/products.css'
import ProductInfo from './ProductInfo';

class Products extends Component {
    constructor(props){
        super(props)
        this.state = {
            data: null,
            id:null,
        }
        this.handleClickMore = this.handleClickMore.bind(this)
        this.myRef = React.createRef();
    }

    handleClickMore(id){
        this.setState({
            id:id
        })
        let off = 0;
        if(window.innerWidth > 930 ){
            off = (this.myRef.current.parentElement.clientHeight / 1 )
        }else {
            off = (this.myRef.current.parentElement.clientHeight / 1.6 )

        }
        window.scrollTo({
            top:this.myRef.current.offsetTop - off, 
            behavior: "smooth"
        })
    }

    componentDidMount(){
        fetch(`${process.env.PUBLIC_URL}/JSON/products.json`)
        .then(response => response.json())
        .then(data => 
            this.setState({
            data:data.products
        }));
    }


  render() {
    const {data,id} = this.state;
    return (
      <div>
        {
           data !== null ?
            
            <Router>
               <div >
               <p className='title'>Best <span className='title__sp--yellow'>Buy</span></p>
               <div className='products'>
        
        {
            data.map((d)=>{
                return(
                    <div key={d.id} className='products__wrapper'>
                        <div className={` ${id === d.id ? 'product__full--desc' : 'product__single' }`}>
                            <img src={`${process.env.PUBLIC_URL}/products/${d.prefix}${d.sufix}`} className='product__image' alt={d.name} />
                            <p className='product__name'>{d.name}</p>
                            <div className={id === d.id? 'hidde': ''}>
                                <Link to={`/${d.name}`} className='learn__more--button product__button' onClick={() => this.handleClickMore(d.id)} >Read more</Link>
                            </div>
                        </div>
                    </div>
                )
            })
        }

        {
            data.map((d)=> {
                return (
                    <Route 
                    key={d.id} 
                    exact path={`/${d.name}`} 
                    render={(props) => <ProductInfo {...props} data={d}  />}
                    />
                )
            })
        }
        
    </div>
        <div id='asdfasdfasdf' ref={this.myRef}></div>
            
            
                
            </div>
          </Router>
            
            :
            "loading"
        }
      </div>
    );
  }

}

export default Products;
