import React, { Component } from 'react';
import {NavLink } from 'react-router-dom'
import '../Assets/css/nav.css'
import Logo from '../Assets/images/logo.png'

let windowWidth = window.innerWidth;

class Nav extends Component {
  constructor(props){
    super(props)

    this.state = {
      height:'auto',
      padding: 0,
      isOpen:false
    }
    this.handleScroll = this.handleScroll.bind(this)
    this.handleResize = this.handleResize.bind(this)
    this.handleLogoClick = this.handleLogoClick.bind(this)
    this.handleLinkClick = this.handleLinkClick.bind(this)
  }
  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
    window.addEventListener('resize',this.handleResize)
  }

  handleResize(){
    windowWidth = window.innerWidth;
    if(windowWidth < 768){
      this.setState({height:'auto',padding:0})
    } else {
      this.setState({height:'80px',padding:20})
    }
  }
  handleScroll(){
    const top = window.pageYOffset;
    if(windowWidth > 768){
      if(top > 150){
        this.setState({height:'auto',padding:0})
      } else if(top < 150) {
        this.setState({height:'80px',padding:20})
      }
    } 
  }
  handleLogoClick(){
    if(windowWidth < 768){
      this.setState(prevState => ({
        isOpen: !prevState.isOpen
      }));  
    }
  }
  handleLinkClick(link){
    console.log(link);
    
  }
  render() {
    const {isOpen, height, padding} = this.state;
    return (

      <nav className={`nav ${isOpen ? 'nav__active' : ''}`} style={{height:height, paddingTop:`${padding}px`}}>
      <div className={`nav__logo--wrapper ${isOpen ? 'move__logo' : ''}`}>
        <div onClick={this.handleLogoClick}><img src={Logo} alt='company logo' /></div>
      </div>
       <ul className='nav__items'>
           <li className='nav__links'><NavLink activeClassName='active__link' onClick={()=>this.handleLinkClick('home')} to='/Home' > Home </NavLink></li>
           <li className='nav__links'><NavLink activeClassName='active__link' onClick={()=>this.handleLinkClick('products')} to='/products' > Products </NavLink></li>
           <li className='nav__links'><NavLink activeClassName='active__link' onClick={()=>this.handleLinkClick('link')} to='eshop' > eShop </NavLink></li>
           <li className='nav__links'><NavLink activeClassName='active__link' onClick={()=>this.handleLinkClick('link2')} to='support' > Support </NavLink></li>
       </ul>
      </nav>

    );
  }
}

export default Nav;
